import FlexWrapper from "./FlexWrapper";
import { Box, Heading, theme } from "@mechatronick/datum-design-system";
import React from "react";
import styled from "styled-components";
import Fade from "react-reveal/Fade";

const StyledDiv = styled.div`
  height: 100px;
  width: 100px;
  margin: 0 auto;
  filter: grayscale(70%);
  box-shadow: ${theme.boxShadows[0]};
  transition: all 0.4s ease;
  &:hover {
    transition: all 0.4s ease;
    filter: none;
    box-shadow: ${theme.boxShadows[2]};
  }
`;

const Label = styled(Heading.h4)`
  line-height: 1.2;
  margin: ${theme.space[2]}px auto;
  padding-bottom: ${theme.space[3]}px;
`;

const MultipleImageAndTextCombo = props => {
  const { data } = props;
  return (
    <>
      <FlexWrapper
        style={{
          textAlign: "center",
        }}
        wrap={"wrap"}
      >
        {data.map((eachProperty, index) => {
          return (
            <Box key={eachProperty.title} width={[1 / 3, 1 / 4, 1 / 6]}>
              <Fade bottom>
                <StyledDiv>

                  <img
                    style={{
                      position: "relative",
                      top: "50%",
                      transform: "translateY(-50%)",
                    }}
                    title={eachProperty.title}
                    alt={eachProperty.title}
                    src={eachProperty.svg}
                  />

                </StyledDiv>
              </Fade>
              <Fade delay={300}>
                <Label>{eachProperty.title}</Label>
              </Fade>
            </Box>
          );
        })}
      </FlexWrapper>
    </>
  );
};

export default MultipleImageAndTextCombo;
