import {graphql, useStaticQuery} from "gatsby"

export const useOperatingSystemsPicsHook = () => (
    useStaticQuery(
        graphql`            
            query {
                CentOS: file(relativePath: { eq: "Operating systems/CentOS.png" }) {
                    ...squareImage100
                }
                RedhatLinux: file(relativePath: { eq: "Operating systems/RedhatLinux.png" }) {
                    ...squareImage100
                }
                WindowServer: file(relativePath: { eq: "Operating systems/WindowServer.png" }) {
                    ...squareImage100
                }
            }
        `
    )
);